import React from "react";

const Pepple = () => {
  const list = [
    { name: "a", age: 25 },
    { name: "b", age: 20 },
    { name: "c", age: 30 },
    { name: "d", age: 18 }
  ];

  return (
    <div>
      {list.map((i, idx) => (
        <div key={idx}>
          {i.name} {i.age}
        </div>
      ))}
    </div>
  );
};

export default Pepple;
